.Banner.about-page{
    box-shadow: none;

    &:before {
        background-image: url('../assets/Mountain.png');
        filter: brightness(100%);
    }

    p {
        display: none;
    }
}

body {
    display: flex;
    height: auto;
    flex-direction: column;
    min-height: 100vh;
}

.about {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    height: auto;
    gap: 10px;
    margin-top: 40px;

    .collapse-bar__item__content.expanded {
        transform: scaleY(1);
        height: auto;
        padding-bottom: 10px;
        margin: 0 0 20px 0;
    }

    .collapse-bar__item {
        width: 100%;
    }
}

@media (max-width: 425px) {
    .about {
        flex: 1 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        width: 100%;
        height: auto;
        gap: 10px;
        margin-top: 50px;
    
        .collapse-bar__item__content.expanded {
            transform: scaleY(1);
            height: auto;
            padding-bottom: 10px;
            margin: 0 0 5px 0;
        }
        
    .collapse-bar__text {
        font-size: 12px;
        font-weight: 400;
        padding-bottom: 1px;
    }
}
}
